/* eslint-disable import/no-anonymous-default-export */


export default (data) => {
  // 
  let result = [];

  // 
  data.forEach(item => {
    // 
    let year = item.date.slice(0, 4);
    let month = item.date.slice(5, 7);

    // 
    let exist = result.find(item => item.year === year && item.month === month);

    // 
    if (!exist) {
      let obj = {
        year: year,
        month: month,
        income: 0,
        expend: 0,
        list: []
      };
      result.push(obj);
      exist = obj;
    }

    // 
    if (item.type === '') {
      exist.income += item.amount;
    } else {
      exist.expend += item.amount;
    }

    // 
    exist.list.push({
      date: item.date,
      time: item.time,
      type: item.type,
      name: item.name,
      amount: item.amount,
      balance: item.balance,
      cardNo: item.cardNo,
      category: item.category
    });
  });
  return result
}